   $(document).ready(function() {
      $('a, button').on('click', function(evt) {
         evt.preventDefault();
      });

      var navHeightis = $(".navbar").height();
     $(window).bind("scroll", function() {
       //var navHeight = $(window).height() - 336;
       //console.log(navHeight);
       if ($(window).scrollTop() > navHeightis) {
         $(".navbar").addClass("fixed-navbar");
         $(".banner_list").addClass("eff-navbar");
       } else {
         $(".navbar").removeClass("fixed-navbar");
         $(".banner_list").removeClass("eff-navbar");
       }
     });
   });
